import Grid from "@material-ui/core/Grid";
import {
    CheckBoxField, SelectWithOptionsField, SimpleTextField,
} from "../userListDesignSettingsDialogFields";
import React from "react";
import {BackgroundImage, UserListDesignSettings} from "../user_list_settings_schema";


/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function BackgroundImgTab(props) {
    const {settings, handleChange} = props;

    return <Grid container className="tab-content-container">
        <Grid className="grid-item" item xs={6}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.show}
                label={'Background Image'}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.parallax}
                label={'Parallax'}
                disabled={true}
            />
        </Grid>

        <Grid className="grid-item" item xs={12}>
            <SimpleTextField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.url}
                label={'Url'}
                html_id_prefix={'background-image-url'}
            />
        </Grid>

        <Grid className="grid-heading" item xs={12}>
            <p className="with-margin">Options</p>
        </Grid>

        <Grid className="grid-item" item xs={6}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Size'}
                html_id_prefix={'background-image-size'}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.size}
                options={['contain', 'cover', 'initial']}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Attachment'}
                html_id_prefix={'background-image-attachment'}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.attachment}
                options={['fixed', 'scroll']}
            />
        </Grid>

        <Grid className="grid-heading" item xs={12}>
            <p className="with-margin">Position</p>
        </Grid>

        <Grid className="grid-item" item xs={3}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Position X'}
                html_id_prefix={'background-image-position-x'}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.position_x}
                options={['center', 'left', 'right']}
            />
        </Grid>
        <Grid className="grid-item" item xs={3}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Position Y'}
                html_id_prefix={'background-image-position-y'}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.position_y}
                options={['center', 'top', 'bottom']}
            />
        </Grid>
        <Grid className="grid-item" item xs={3}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Repeat X'}
                html_id_prefix={'background-image-repeat-x'}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.repeat_x}
                options={['no-repeat', 'repeat']}
            />
        </Grid>
        <Grid className="grid-item" item xs={3}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Repeat Y'}
                html_id_prefix={'background-image-repeat-y'}
                key_1={UserListDesignSettings.background_image}
                key_2={BackgroundImage.repeat_y}
                options={['no-repeat', 'repeat']}
            />
        </Grid>
    </Grid>
}