import React from "react";

import Grid from "@material-ui/core/Grid";
import {
    ColorPickerField, CSS_FONTS, NumberWithUnitField, SelectWithOptionsField, SimpleTextField
} from "../userListDesignSettingsDialogFields";
import {AboutText, UserListDesignSettings} from "../user_list_settings_schema";


/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function MessageTab(props) {
    const {settings, handleChange} = props;

    return <Grid container className="tab-content-container">
        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Top Margin'}
                html_id_prefix={'about-text-top-margin'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.margin_top}
                with_units={true}
                units={['rem', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Font Family'}
                html_id_prefix={'about-font-family'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.font_family}
                options={CSS_FONTS}
            />
        </Grid>

        <Grid className="grid-item" item xs={12}>
            <SimpleTextField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header}
                label={'Heading'}
                html_id_prefix={'heading'}
            />
        </Grid>

        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Left'}
                html_id_prefix={'header-left'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_left}
                with_units={true}
                units={['rem', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Right'} html_id_prefix={'header-right'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_right}
                with_units={true}
                units={['rem', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Align'}
                html_id_prefix={'header-align'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_align}
                options={['center', 'left', 'right', 'justify']}
            />
        </Grid>

        <Grid className="grid-item color-picker-field" item xs={4}>
            <ColorPickerField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_font_color}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Font Size'}
                html_id_prefix={'header-font-size'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_font_size}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Style'}
                html_id_prefix={'header-font-style'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_font_style}
                options={['initial', 'italic']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Weight'}
                html_id_prefix={'header-font-weight'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_font_weight}
                options={['100', '200', '300', '400', '500', '600', '700', '800', '900']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Variant'}
                html_id_prefix={'header-font-variant'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.header_font_variant}
                options={['initial', 'all-petite-caps', 'all-small-caps', 'petite-caps', 'small-caps', 'unicase']}
            />
        </Grid>

        <Grid className="grid-item" item xs={12}>
            <SimpleTextField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text}
                label={'Text'}
                html_id_prefix={'text'}
                multiline={true}
            />
        </Grid>

        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Left'}
                html_id_prefix={'text-left'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_left}
                with_units={true}
                units={['rem', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Right'}
                html_id_prefix={'text-right'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_right}
                with_units={true}
                units={['rem', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Align'}
                html_id_prefix={'header-align'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_align}
                options={['center', 'left', 'right', 'justify']}
            />
        </Grid>

        <Grid className="grid-item color-picker-field" item xs={4}>
            <ColorPickerField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_font_color}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Font Size'}
                html_id_prefix={'header-font-size'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_font_size}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Style'}
                html_id_prefix={'text-font-style'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_font_style}
                options={['initial', 'italic']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Weight'}
                html_id_prefix={'text-font-weight'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_font_weight}
                options={['100', '200', '300', '400', '500', '600', '700', '800', '900']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Variant'}
                html_id_prefix={'text-font-variant'}
                key_1={UserListDesignSettings.about_text}
                key_2={AboutText.text_font_variant}
                options={['initial', 'all-petite-caps', 'all-small-caps', 'petite-caps', 'small-caps', 'unicase']}
            />
        </Grid>
    </Grid>
}
