import React from "react";

import {faServer} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

import {
    BRANCH_ANIME,
    BRANCH_CHARACTERS,
    BRANCH_EROGE,
    BRANCH_MANGA, BRANCH_PEOPLE,
    USER_ANIME_STATUSES,
    USER_MANGA_STATUSES
} from "../../../globals";
import {
    ANIME_DISPLAY,
    CHARACTERS_DISPLAY,
    EROGE_DISPLAY,
    MANGA_DISPLAY,
    PERSON_DISPLAY
} from "../../../urls";
import {
    Anime,
    Character,
    Eroge,
    Manga,
    ANIME_SHORT_STATUS,
    MANGA_SHORT_STATUS,
    Person,
    UserAnimeDisplaySettingsSchema,
    UserMangaDisplaySettingsSchema,
    UserErogeDisplaySettingsSchema
} from "../../../models";
import {formatContentDateByKnown, isEmpty} from "../../functions";
import {FilterSchema} from "../filters/field_names";
import CardUserEditTitleEntry from "./CardUserEditTitleEntry";
import store from "../../../store";


export function getUserStatusChip(user_status) {
    switch (user_status) {
        case USER_ANIME_STATUSES.completed:
            return {label: user_status, color: 'primary'};
        case USER_ANIME_STATUSES.watching:
            return {label: user_status, color: 'success'};
        case USER_MANGA_STATUSES.reading:
            return {label: user_status, color: 'success'};
        case USER_ANIME_STATUSES.on_hold:
            return {label: user_status, color: 'warning'};
        case USER_ANIME_STATUSES.dropped:
            return {label: user_status, color: 'danger'};
        case USER_ANIME_STATUSES.plan_to_watch:
            return {label: user_status, color: 'default'};
        case USER_MANGA_STATUSES.plan_to_read:
            return {label: user_status, color: 'default'};
        default:
            return null
    }
}


/**
 *
 * @param props.branch {String} Title Branch
 * @param props.id {Number} Title ID
 * @param props.title {String} Title
 * @param props.user_status {String} Status of the user for this anime (Completed, Dropped, etc)
 * @param props.hideUserButtons {Function} Function for running on close of the dialog
 * @returns {JSX.Element}
 * @constructor
 */
export function UserCardButtons(props) {
    const {branch, id, title, user_status, hideUserButtons} = props;

    switch (branch) {
        case BRANCH_ANIME:
            return <CardUserEditTitleEntry id={id} branch={BRANCH_ANIME} title={title} user_status={user_status}
                                           hideUserButtons={hideUserButtons}/>;
        case BRANCH_MANGA:
            return <CardUserEditTitleEntry id={id} branch={BRANCH_MANGA} title={title} user_status={user_status}
                                           hideUserButtons={hideUserButtons}/>;
        default:
            return null;
    }
}


export function formatTitle(branch, title, user_status_param, design_settings = null) {
    let user_status = user_status_param;
    let start_date = '';
    let end_date = '';
    let card_title = {
        id: 0,
        link: '',
        cover_images: [],
        cdn_cover_images: [],
        title: '',
        chipset_header: [],
        chipset_one: [],
        chipset_two: [],
        chipset_three: [],
        chipset_four: []
    };
    let user_settings = {};

    switch (branch) {
        case BRANCH_ANIME:
            if (isEmpty(design_settings)) user_settings = store.getState().users.user_anime_display_settings;
            else {
                user_settings = {
                    [UserAnimeDisplaySettingsSchema.type]: design_settings.title_type,
                    [UserAnimeDisplaySettingsSchema.episodes]: design_settings.title_episodes,
                    [UserAnimeDisplaySettingsSchema.mins_per_ep]: design_settings.title_mins_per_ep,
                    [UserAnimeDisplaySettingsSchema.rating]: design_settings.title_rating,
                    [UserAnimeDisplaySettingsSchema.genres]: design_settings.title_genres,
                    [UserAnimeDisplaySettingsSchema.mal_score]: design_settings.title_mal_score,
                    [UserAnimeDisplaySettingsSchema.aired]: design_settings.title_aired,
                };
                if (!design_settings.title_status) user_status = null
            }

            start_date = formatContentDateByKnown(title[Anime.air_start], title[Anime.air_start_known], true);
            end_date = formatContentDateByKnown(title[Anime.air_end], title[Anime.air_end_known], true);

            card_title.id = title[Anime.id];
            card_title.link = ANIME_DISPLAY(title[Anime.id]);
            card_title.cover_images = title[Anime.anime_cover_images];
            card_title.cdn_cover_images = title[Anime.anime_cdn_cover_images];
            card_title.title = title[Anime.title];
            try {
                card_title.chipset_header = [{
                    label: ANIME_SHORT_STATUS[title[Anime.status]][FilterSchema.label],
                    color: ANIME_SHORT_STATUS[title[Anime.status]][FilterSchema.color]
                }];
            } catch (e) {
            }
            if (title[Anime.pending_by_MAL])
                card_title.chipset_header.push({label: 'M-PEN', color: 'warning'});
            if (title[Anime.deleted_by_MAL])
                card_title.chipset_header.push({label: 'M-DEL', color: 'danger'});
            if (title[Anime.anime_on_server])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});
            if (user_status)
                card_title.chipset_header.push(getUserStatusChip(user_status));

            if (user_settings[UserAnimeDisplaySettingsSchema.type])
                card_title.chipset_one.push(title[Anime.type]);
            if (user_settings[UserAnimeDisplaySettingsSchema.episodes] && title[Anime.episodes])
                card_title.chipset_one.push(`${title[Anime.episodes]}ep`);
            if (user_settings[UserAnimeDisplaySettingsSchema.mins_per_ep] && title[Anime.minutes_per_episode])
                card_title.chipset_one.push(`${title[Anime.minutes_per_episode]}m`);
            if (user_settings[UserAnimeDisplaySettingsSchema.rating])
                card_title.chipset_one.push(title[Anime.rating_short]);

            if (user_settings[UserAnimeDisplaySettingsSchema.genres])
                card_title.chipset_two = title[Anime.genres_csv] ? title[Anime.genres_csv].split(', ') : [];

            if (user_settings[UserAnimeDisplaySettingsSchema.mal_score] && title[Anime.score])
                card_title.chipset_three.push(`MAL: ${title[Anime.score]}`);

            if (user_settings[UserAnimeDisplaySettingsSchema.aired] && start_date)
                card_title.chipset_four.push(`${start_date}${end_date ? ' to ' + end_date : ''}`);

            return card_title;
        case BRANCH_MANGA:
            // user_settings = store.getState().users.user_manga_display_settings;
            if (isEmpty(design_settings)) user_settings = store.getState().users.user_manga_display_settings;
            else {
                user_settings = {
                    [UserMangaDisplaySettingsSchema.type]: design_settings.title_type,
                    [UserMangaDisplaySettingsSchema.volumes]: design_settings.title_volumes,
                    [UserMangaDisplaySettingsSchema.chapters]: design_settings.title_chapters,
                    [UserMangaDisplaySettingsSchema.genres]: design_settings.title_genres,
                    [UserMangaDisplaySettingsSchema.mal_score]: design_settings.title_mal_score,
                    [UserMangaDisplaySettingsSchema.aired]: design_settings.title_aired,
                };
                if (!design_settings.title_status) user_status = null
            }

            start_date = formatContentDateByKnown(title[Manga.air_start], title[Manga.air_start_known], true);
            end_date = formatContentDateByKnown(title[Manga.air_end], title[Manga.air_end_known], true);

            card_title.id = title[Manga.id];
            card_title.link = MANGA_DISPLAY(title[Manga.id]);
            card_title.cover_images = title[Manga.manga_cover_images];
            card_title.cdn_cover_images = title[Manga.manga_cdn_cover_images];
            card_title.title = title[Manga.title];
            try {
                card_title.chipset_header = [{
                    label: MANGA_SHORT_STATUS[title[Manga.status]][FilterSchema.label],
                    color: MANGA_SHORT_STATUS[title[Manga.status]][FilterSchema.color]
                }];
            } catch (e) {
            }
            if (title[Manga.pending_by_MAL])
                card_title.chipset_header.push({label: 'M-PEN', color: 'warning'});
            if (title[Manga.deleted_by_MAL])
                card_title.chipset_header.push({label: 'M-DEL', color: 'danger'});
            if (title[Manga.manga_on_server])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});
            if (user_status)
                card_title.chipset_header.push(getUserStatusChip(user_status));

            if (user_settings[UserMangaDisplaySettingsSchema.type])
                card_title.chipset_one.push(title[Manga.type]);
            if (user_settings[UserMangaDisplaySettingsSchema.volumes] && title[Manga.volumes])
                card_title.chipset_one.push(`${title[Manga.volumes]}v`);
            if (user_settings[UserMangaDisplaySettingsSchema.chapters] && title[Manga.chapters])
                card_title.chipset_one.push(`${title[Manga.chapters]}c`);

            if (user_settings[UserMangaDisplaySettingsSchema.genres])
                card_title.chipset_two = title[Manga.genres_csv] ? title[Manga.genres_csv].split(', ') : [];

            if (user_settings[UserMangaDisplaySettingsSchema.mal_score] && title[Manga.score])
                card_title.chipset_three.push(`MAL: ${title[Manga.score]}`);

            if (user_settings[UserMangaDisplaySettingsSchema.aired] && start_date)
                card_title.chipset_four.push(`${start_date}${end_date ? ' to ' + end_date : ''}`);

            return card_title;
        case BRANCH_CHARACTERS:
            card_title.id = title[Character.id];
            card_title.link = CHARACTERS_DISPLAY(title[Character.id]);
            card_title.cover_images = title[Character.character_cover_images];
            card_title.cdn_cover_images = title[Character.character_cdn_cover_images];
            card_title.title = title[Character.short_name] ?
                title[Character.short_name] : title[Character.character_name];
            return card_title;
        case BRANCH_PEOPLE:
            card_title.id = title[Person.id];
            card_title.link = PERSON_DISPLAY(title[Person.id]);
            card_title.cover_images = title[Person.person_cover_images];
            card_title.cdn_cover_images = title[Person.person_cdn_cover_images];
            card_title.title = title[Person.person_name];
            return card_title;
        case BRANCH_EROGE:
            user_settings = store.getState().users.user_eroge_display_settings;
            card_title.id = title[Eroge.id];
            card_title.link = EROGE_DISPLAY(title[Eroge.id]);
            card_title.cover_images = title[Eroge.eroge_cover_images];
            card_title.title = title[Eroge.eroge_name].replace('[Android]', '').trim();
            if (title[Eroge.downloaded])
                card_title.chipset_header.push({label: <FontAwesomeIcon icon={faServer}/>, color: 'secondary'});

            if (user_settings[UserErogeDisplaySettingsSchema.type])
                card_title.chipset_one.push(title[Manga.type]);
            if (user_settings[UserErogeDisplaySettingsSchema.platforms] && title[Eroge.platforms])
                card_title.chipset_one.push(title[Eroge.platforms]);

            if (user_settings[UserErogeDisplaySettingsSchema.tags] && title[Eroge.tags])
                card_title.chipset_two = title[Eroge.tags] ? title[Eroge.tags].split(', ') : [];

            if (user_settings[UserErogeDisplaySettingsSchema.hg_score] && title[Eroge.rating])
                card_title.chipset_three.push(`HG ${title[Eroge.rating]}`);

            if (user_settings[UserErogeDisplaySettingsSchema.language] && title[Eroge.language])
                card_title.chipset_four.push(title[Eroge.language]);
            if (user_settings[UserErogeDisplaySettingsSchema.censorship] && title[Eroge.censorship])
                card_title.chipset_four.push(title[Eroge.censorship]);

            return card_title;
        default:
            return card_title
    }
}
