import Grid from "@material-ui/core/Grid";
import {
    CheckBoxField, NumberWithUnitField, SelectWithOptionsField, SimpleTextField,
} from "../userListDesignSettingsDialogFields";
import React, {useState} from "react";
import {AboutText, OverlayImage, UserListDesignSettings} from "../user_list_settings_schema";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from "@material-ui/core/FormControlLabel";


/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function OverlayImgTab(props) {
    const {settings, handleChange} = props;
    const r = /\d+/;
    const [with_units, setWithUnits] = useState(!!settings[UserListDesignSettings.overlay_image][OverlayImage.size].match(r));

    return <Grid container className="tab-content-container">
        <Grid className="grid-item" item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.overlay_image}
                key_2={OverlayImage.show}
                label={'Overlay Image'}
            />
        </Grid>
        <Grid className="grid-item" item xs={8}/>

        <Grid className="grid-item" item xs={12}>
            <SimpleTextField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.overlay_image}
                key_2={OverlayImage.url}
                label={'Url'}
                html_id_prefix={'overlay-image-url'}
            />
        </Grid>

        <Grid className="grid-heading" item xs={12}>
            <p className="with-margin">Options</p>
        </Grid>

        <Grid className="grid-item" item xs={4}>
            <FormControlLabel
                value={'With units'}
                control={<Checkbox color="primary"/>}
                label={'With units'}
                labelPlacement="start"
                checked={with_units}
                onChange={(e) => {
                    // noinspection JSUnresolvedVariable
                    setWithUnits(e.target.checked)
                }}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            {with_units ? <NumberWithUnitField
                    settings={settings}
                    handleChange={handleChange}
                    label={'Size'}
                    html_id_prefix={'about-text-top-margin'}
                    key_1={UserListDesignSettings.overlay_image}
                    key_2={OverlayImage.size}
                    with_units={true}
                    units={['rem', 'px']}
                /> :
                <SelectWithOptionsField
                    settings={settings}
                    handleChange={handleChange}
                    label={'Image Fit'}
                    html_id_prefix={'overlay-image-fit'}
                    key_1={UserListDesignSettings.overlay_image}
                    key_2={OverlayImage.size}
                    options={['contain', 'cover', 'fill', 'initial', 'revert', 'scale-down']}
                />}
        </Grid>

        <Grid className="grid-heading" item xs={12}>
            <p className="with-margin">Position</p>
        </Grid>

        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Horizontal'}
                html_id_prefix={'about-text-top-margin'}
                key_1={UserListDesignSettings.overlay_image}
                key_2={OverlayImage.position_x}
                with_units={true}
                units={['rem', '%', 'px']}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Vertical'}
                html_id_prefix={'about-text-top-margin'}
                key_1={UserListDesignSettings.overlay_image}
                key_2={OverlayImage.position_y}
                with_units={true}
                units={['rem', '%', 'px']}
            />
        </Grid>

        <Grid className="grid-item" item xs={6}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Repeat X'}
                html_id_prefix={'background-image-repeat-x'}
                key_1={UserListDesignSettings.overlay_image}
                key_2={OverlayImage.repeat_x}
                options={['no-repeat', 'repeat']}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Repeat Y'}
                html_id_prefix={'background-image-repeat-y'}
                key_1={UserListDesignSettings.overlay_image}
                key_2={OverlayImage.repeat_y}
                options={['no-repeat', 'repeat']}
            />
        </Grid>
    </Grid>
}