import {
    Anime,
    ApiResult, Manga,
    UserAnimeDisplaySettingsSchema, UserAnimeEntry,
    UserErogeDisplaySettingsSchema,
    UserMangaDisplaySettingsSchema, UserMangaEntry
} from "../../../models";
import Grow from "@material-ui/core/Grow";
import GridListTile from "@material-ui/core/GridListTile";
import TitleCard from "../../layout/cards/TitleCard";
import {getTitleCoverImage, isEmpty} from "../../functions";
import GridList from "@material-ui/core/GridList";
import React from "react";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faEdit, faImage} from "@fortawesome/free-solid-svg-icons";
import TableBody from "@material-ui/core/TableBody";
import TableContainer from "@material-ui/core/TableContainer";
import TitleRow from "../../layout/rows/TitleRow";
import {BRANCH_ANIME, BRANCH_EROGE, BRANCH_MANGA} from "../../../globals";
import {UserListTableHead, UserListTableRow} from "./anime/tableView";


function getTitleAndUserStatusFromBranch(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            return [UserAnimeEntry.anime, Anime.id]
        case BRANCH_MANGA:
            return [UserMangaEntry.manga, Manga.id]
    }
}


function noResult(branch, filtered_titles) {
    switch (branch) {
        case BRANCH_ANIME:
            if (isEmpty(filtered_titles[ApiResult.results]?.[0]?.[UserAnimeEntry.anime]))
                return true
            break
        case BRANCH_MANGA:
            if (isEmpty(filtered_titles[ApiResult.results]?.[0]?.[UserMangaEntry.manga]))
                return true
            break
        default:
            return true
    }

    return false
}


/**
 *
 * @param props.gridlist_cols
 * @param props.filtered_titles
 * @param props.branch
 * @param props.current_page
 * @param props.items_per_page
 * @param props.user_entry_statuses
 * @param props.list_owner {Boolean}
 * @param props.design_settings {Object}
 * @returns {JSX.Element}
 */
export function UserListCardsView(props) {
    if (noResult(props.branch, props.filtered_titles))
        return null

    const [title_key, id_key] = getTitleAndUserStatusFromBranch(props.branch)

    return <GridList className="list-cards-view" cellHeight={400} cols={props.gridlist_cols} style={{margin: '0 0 10px 0'}}>
        {props.filtered_titles[ApiResult.results].map((entry, i) => (
            <Grow key={i} in {...({timeout: 200 + (i * 50)})}>
                <GridListTile
                    cols={1}
                    className={props.design_settings.list_cards_round_borders ? 'round-borders' : undefined}
                    style={{padding: props.design_settings.list_cards_spacing}}
                >
                    <TitleCard
                        branch={props.branch}
                        title={entry[title_key]}
                        user_buttons={!!props.list_owner}
                        index={i + ((props.current_page * props.items_per_page) - (props.items_per_page - 1))}
                        user_status={props.list_owner ? !isEmpty(props.user_entry_statuses) ?
                            props.user_entry_statuses[`${entry[title_key][id_key]}`] : null : null}
                        design_settings={props.design_settings}
                    />
                </GridListTile>
            </Grow>
        ))}
    </GridList>
}


/**
 *
 * @param props.filtered_titles
 * @param props.branch
 * @param props.current_page
 * @param props.items_per_page
 * @param props.user_entry_statuses
 * @param {Boolean} props.list_owner
 * @param {Object} props.list
 * @param {Object} props.design_settings
 * @returns {JSX.Element}
 * @constructor
 */
export function UserListTableView(props) {
    if (noResult(props.branch, props.filtered_titles))
        return null

    let titles_container_styles = {
        backgroundColor: props.design_settings.list_table_background_color,
        backdropFilter: `blur(${props.design_settings.list_table_backdrop_filter_blur})`
    }

    if (props.design_settings.list_table_round_borders)
        titles_container_styles['borderRadius'] = '15px';

    let classnames = 'list-table-view'
    if (!props.design_settings.list_table_row_lines)
        classnames = classnames + ' list-table-view-no-row-lines'

    const [title_key, id_key] = getTitleAndUserStatusFromBranch(props.branch)

    return <TableContainer className={classnames} style={titles_container_styles}>
        <Table aria-label="user list table">
            <UserListTableHead
                branch={props.branch}
                design_settings={props.design_settings}
                list_owner={props.list_owner}
            />
            <TableBody>
                {props.filtered_titles[ApiResult.results].map((entry, i) => (
                    <UserListTableRow
                        key={i}
                        branch={props.branch}
                        list_owner={props.list_owner}
                        list={props.list}
                        entry={entry}
                        index={i + ((props.current_page * props.items_per_page) - (props.items_per_page - 1))}
                        user_status={props.list_owner ? !isEmpty(props.user_entry_statuses) ?
                            props.user_entry_statuses[`${entry[title_key][id_key]}`] : null : null}
                        design_settings={props.design_settings}
                    />
                ))}
            </TableBody>
        </Table>
    </TableContainer>
}