import React from "react";

import Grid from "@material-ui/core/Grid";
import {
    CheckBoxField,
    ColorPickerField, CSS_FONTS, NumberWithUnitField, SelectWithOptionsField, SimpleTextField
} from "../userListDesignSettingsDialogFields";
import {
    AboutText,
    Cards,
    CoverImage,
    List,
    Table,
    TitleInfoAnime, TitleInfoManga,
    UserListDesignSettings
} from "../user_list_settings_schema";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {faThLarge, faThList} from "@fortawesome/free-solid-svg-icons";
import {CyberButton} from "../../../../layout/common/common";
import {BRANCH_ANIME, BRANCH_MANGA} from "../../../../../globals";


/**
 *
 * @param props.branch {String}
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function ListTab(props) {
    const {settings, handleChange} = props;
    const [progress_key, progress_label] = props.branch === BRANCH_ANIME ?
        [TitleInfoAnime.progress, 'Progress'] : [TitleInfoManga.volumes_progress, 'Progress Volumes']

    return <Grid container className="tab-content-container">
        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Top'}
                html_id_prefix={'list-titles-margin-top'}
                key_1={UserListDesignSettings.list}
                key_2={List.margin_top}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Left'}
                html_id_prefix={'list-titles-padding-left'}
                key_1={UserListDesignSettings.list}
                key_2={List.padding_left}
                with_units={true}
                units={['rem', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Right'}
                html_id_prefix={'list-titles-padding-right'}
                key_1={UserListDesignSettings.list}
                key_2={List.padding_right}
                with_units={true}
                units={['rem', '%']}
            />
        </Grid>

        <Grid className="grid-item" item xs={12}>
            <CyberButton
                classNames="list-type-btn btn-contained-primary" variant="contained"
                onClick={() => {
                    props.handleChange(!settings[UserListDesignSettings.type_cards], UserListDesignSettings.type_cards)
                }}
            >
                <FontAwesomeIcon
                    icon={settings[UserListDesignSettings.type_cards] ? faThList : faThLarge}
                    title={settings[UserListDesignSettings.type_cards] ? 'Table' : 'Cards'}
                />
            </CyberButton>{' '}
            List type {settings[UserListDesignSettings.type_cards] ? 'Cards' : 'Table'}
        </Grid>

        <Grid item xs={12}>
            <p>{settings[UserListDesignSettings.type_cards] ? 'Cards options' : 'Table options'}</p>
        </Grid>

        {settings[UserListDesignSettings.type_cards] ? <>
            <Grid className="grid-item" item xs={4}>
                <NumberWithUnitField
                    settings={settings}
                    handleChange={handleChange}
                    label={'Spacing'}
                    html_id_prefix={'user-list-cards-spacing'}
                    key_1={UserListDesignSettings.cards}
                    key_2={Cards.spacing}
                />
            </Grid>
            <Grid className="grid-item" item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.cards}
                    key_2={Cards.round_borders}
                    label={'Round Borders'}
                />
            </Grid></> : <>
            <Grid className="grid-item color-picker-field" item xs={4} >
                <ColorPickerField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.table}
                    key_2={Table.background_color}
                />
            </Grid>
            <Grid className="grid-item" item xs={8}>
                <NumberWithUnitField
                    settings={settings}
                    handleChange={handleChange}
                    label={'Filter'}
                    html_id_prefix={'user-list-table-backdrop-filter'}
                    key_1={UserListDesignSettings.table}
                    key_2={Table.backdrop_filter_blur}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.table}
                    key_2={Table.round_borders}
                    label={'Round Borders'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.table}
                    key_2={Table.show_images}
                    label={'Show Images'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.table}
                    key_2={Table.text_bubbles}
                    label={'Text Bubbles'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.table}
                    key_2={Table.row_lines}
                    label={'Row Lines'}
                />
            </Grid>
        </>}

        {!settings[UserListDesignSettings.type_cards] ? <>
            <Grid className="grid-heading" item xs={12}>
                <p>My info</p>
            </Grid>

            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.user_score}
                    label={'Score'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.edit_button}
                    label={'Edit Btn.'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={progress_key}
                    label={progress_label}
                />
            </Grid>
            {props.branch === BRANCH_MANGA ? <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoManga.chapters_progress}
                    label={'Progress Chapters'}
                />
            </Grid> : null}

            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.status}
                    label={'Status'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.priority}
                    label={'Priority'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.comments}
                    label={'Comments'}
                />
            </Grid>

            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.tags}
                    label={'Tags'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.started}
                    label={'Started Watching'}
                />
            </Grid>
            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.finished}
                    label={'Finished Watching'}
                />
            </Grid>

            <Grid item xs={4}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.added}
                    label={'Added to List'}
                />
            </Grid>
            <Grid item xs={8}>
                <CheckBoxField
                    settings={settings}
                    handleChange={handleChange}
                    key_1={UserListDesignSettings.title_info}
                    key_2={TitleInfoAnime.days}
                    label={'Days'}
                />
            </Grid>
        </> : null}

        <Grid className="grid-heading" item xs={12}>
            <p>Title info</p>
        </Grid>

        <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoAnime.type}
                label={'Type'}
            />
        </Grid>
        {props.branch === BRANCH_ANIME ? <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoAnime.episodes}
                label={'Episodes'}
            />
        </Grid> : null}
        {props.branch === BRANCH_MANGA ? <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoManga.volumes}
                label={'Volumes'}
            />
        </Grid> : null}
        {props.branch === BRANCH_MANGA ? <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoManga.chapters}
                label={'Chapters'}
            />
        </Grid> : null}
        {props.branch === BRANCH_ANIME ? <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoAnime.mins_per_ep}
                label={'Mins per Ep.'}
            />
        </Grid> : null}

        {props.branch === BRANCH_ANIME ? <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoAnime.rating}
                label={'Rating'}
            />
        </Grid> : null}
        <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoAnime.genres}
                label={'Genres'}
            />
        </Grid>
        <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoAnime.mal_score}
                label={'MAL Score'}
            />
        </Grid>

        <Grid item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.title_info}
                key_2={TitleInfoAnime.aired}
                label={'Aired'}
            />
        </Grid>
    </Grid>
}
