export function getInitialSelected(branch) {
    switch (branch) {
        case BRANCH_ANIME:
            return USER_ANIME_STATUSES.watching;
        case BRANCH_MANGA:
            return USER_MANGA_STATUSES.reading;
        default:
            return null
    }
}


export function getUserListDesignSettingsFromBranch(branch, anime_settings, manga_settings) {
    switch (branch) {
        case BRANCH_ANIME:
            return anime_settings;
        case BRANCH_MANGA:
            return manga_settings;
        default:
            return {}
    }
}


export function getUserListResultsPerPageFromBranch(branch, visited_user) {
    let number = 100;
    switch (branch) {
        case BRANCH_ANIME:
            number = parseInt(visited_user[User.profile][Profile.user_anime_list_design_settings][UserListDesignSettings.results_per_page])
            if (isNaN(number)) return 100
            return number;
        case BRANCH_MANGA:
            number = parseInt(visited_user[User.profile][Profile.user_manga_list_design_settings][UserListDesignSettings.results_per_page]);
            if (isNaN(number)) return 100
            return number;
        default:
            return 100
    }
}


