/**
 *
 * @param value {String}
 */
function getInitialUnit(value) {
    try {
        if (value.indexOf('rem') >= 0) return 'rem';
        if (value.indexOf('%') >= 0) return '%';
        if (value.indexOf('vh') >= 0) return 'vh';
        if (value.indexOf('px') >= 0) return 'px';
    } catch (e) {
    }
    return 'px';
}

/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @param props.key_1 {String}
 * @param props.key_2 {String}
 * @param props.label {String}
 * @param props.html_id_prefix {String}
 * @param props.with_units {Boolean}
 * @param props.units {Array}
 * @returns {JSX.Element}
 * @constructor
 */
export function NumberWithUnitField(props) {
    const value = props.key_2 ? props.settings[props.key_1]?.[props.key_2] : props.settings[props.key_1];
    const [unit, setUnit] = useState(getInitialUnit(value));
    let classnames = 'form-control form-control-outlined';
    if (props.with_units)
        classnames = 'form-control form-control-outlined form-control-left';

    return <><FormControl className={classnames} variant="outlined" size="small">
        <InputLabel htmlFor={`${props.html_id_prefix}-label`}>{props.label}</InputLabel>
        <OutlinedInput
            id={`${props.html_id_prefix}-label`}
            type="number"
            labelWidth={0}
            endAdornment={props.with_units ? null : <InputAdornment position="end">{unit}</InputAdornment>}
            value={parseFloat(value)}
            onChange={(e) => {
                props.key_2 ?
                    props.handleChange(`${e.target.value}${unit}`, props.key_1, props.key_2) :
                    props.handleChange(`${e.target.value}${unit}`, props.key_1)
            }}
        />
    </FormControl>
        {props.with_units ?
            <FormControl className="form-control form-control-outlined form-control-right" variant="outlined"
                         size="small">
                <InputLabel id={`${props.html_id_prefix}-unit-select`}>Unit</InputLabel>
                <Select
                    id={`${props.html_id_prefix}-unit-select`}
                    labelId={`${props.html_id_prefix}-unit-select`}
                    className="select-form-input"
                    value={unit}
                    onClick={(e) => {
                        // noinspection JSUnresolvedVariable
                        setUnit(e.target.value)
                    }}
                >
                    {props.units.map((unit_choice, x) => (
                        <MenuItem key={x} value={unit_choice}>{unit_choice}</MenuItem>
                    ))}
                </Select>
            </FormControl> : null}</>
}

/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @param props.key_1 {String}
 * @param props.key_2 {String}
 * @param props.label {String}
 * @param props.html_id_prefix {String}
 * @param props.multiline {Boolean}
 * @returns {JSX.Element}
 * @constructor
 */
export function SimpleTextField(props) {
    const value = props.key_2 ? props.settings[props.key_1]?.[props.key_2] : props.settings[props.key_1];
    return <TextField
        label={props.label}
        className="form-control"
        value={value}
        multiline={props.multiline}
        onChange={(e) => {
            props.key_2 ?
                props.handleChange(e.target.value, props.key_1, props.key_2) :
                props.handleChange(e.target.value, props.key_1)
        }}
    />
}

/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @param props.key_1 {String}
 * @param props.key_2 {String}
 * @param props.label {String}
 * @param props.html_id_prefix {String}
 * @param props.options {Array}
 * @returns {JSX.Element}
 * @constructor
 */
export function SelectWithOptionsField(props) {
    const value = props.key_2 ? props.settings[props.key_1]?.[props.key_2] : props.settings[props.key_1];
    return <FormControl className="form-control form-control-outlined" variant="outlined" size="small">
        <InputLabel htmlFor={`${props.html_id_prefix}-label`}>{props.label}</InputLabel>
        <Select
            id={`${props.html_id_prefix}-label`}
            className="select-form-input"
            label={props.label}
            value={value}
            onChange={(e) => {
                props.key_2 ?
                    props.handleChange(e.target.value, props.key_1, props.key_2) :
                    props.handleChange(e.target.value, props.key_1)
            }}
        >
            {props.options.map((option, x) => (
                <MenuItem key={x} value={option}>{option}</MenuItem>
            ))}
        </Select>
    </FormControl>
}

/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @param props.key_1 {String}
 * @param props.key_2 {String}
 * @returns {JSX.Element}
 * @constructor
 */
export function ColorPickerField(props) {
    const value = props.key_2 ? props.settings[props.key_1]?.[props.key_2] : props.settings[props.key_1];
    return <ColorPicker
        value={value}
        onChange={(e) => {
            props.key_2 ?
                props.handleChange(`#${e.hex}`, props.key_1, props.key_2) :
                props.handleChange(`#${e.hex}`, props.key_1)
        }}
    />
}

/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @param props.key_1 {String}
 * @param props.key_2 {String}
 * @param props.label {String}
 * @param props.disabled {Boolean}
 * @returns {JSX.Element}
 * @constructor
 */
export function CheckBoxField(props) {
    const value = props.key_2 ? props.settings[props.key_1]?.[props.key_2] : props.settings[props.key_1];
    return <FormControlLabel
        disabled={props.disabled}
        value={props.label}
        control={<Checkbox color="primary"/>}
        label={props.label}
        labelPlacement="start"
        checked={value}
        onChange={(e) => {
            // noinspection JSUnresolvedVariable
            props.key_2 ?
                props.handleChange(e.target.checked, props.key_1, props.key_2) :
                props.handleChange(e.target.checked, props.key_1)
        }}
    />
}

/**
 *
 * @returns {JSX.Element}
 * @constructor
 */
export function ChooseTemplateField(props) {
    const [value, setValue] = useState('')
    return <FormControl className="form-control form-control-outlined" variant="outlined" size="small">
        <InputLabel htmlFor={'choose-template-label'}>Template</InputLabel>
        <Select
            id={'choose-template-label'}
            className="select-form-input"
            label={'Template'}
            value={value}
            onChange={(e) => {
                console.log('TEMPLATES[value]', e.target.value)
                console.log('TEMPLATES[value]', TEMPLATES[e.target.value])
                setValue(e.target.value)
                const new_settings = {}
                Object.assign(new_settings, TEMPLATES[e.target.value]);
                store.dispatch(setUserListDesignSettings(props.branch, new_settings));
            }}
        >
            {TEMPLATE_NAMES.map((option, x) => (
                <MenuItem key={x} value={option}>{option}</MenuItem>
            ))}
        </Select>
    </FormControl>
}
