import Grid from "@material-ui/core/Grid";
import {
    CheckBoxField, NumberWithUnitField, SelectWithOptionsField, SimpleTextField,
} from "../userListDesignSettingsDialogFields";
import React from "react";
import {CoverImage, UserListDesignSettings} from "../user_list_settings_schema";


/**
 *
 * @param props.settings {Object}
 * @param props.handleChange {Function}
 * @returns {JSX.Element}
 * @constructor
 */
export function CoverImgTab(props) {
    const {settings, handleChange} = props;

    return <Grid container className="tab-content-container">
        <Grid className="grid-item" item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.show}
                label={'Cover Image'}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <CheckBoxField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.parallax}
                label={'Parallax'}
                disabled={true}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}/>

        <Grid className="grid-item" item xs={12}>
            <SimpleTextField
                settings={settings}
                handleChange={handleChange}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.url}
                label={'Url'}
                html_id_prefix={'cover-image-url'}
            />
        </Grid>

        <Grid className="grid-heading" item xs={12}>
            <p className="with-margin">Options</p>
        </Grid>

        <Grid className="grid-item" item xs={4}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Top Margin'}
                html_id_prefix={'cover-img-top-margin'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.margin_top}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Image Fit'}
                html_id_prefix={'cover-image-fit'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.object_fit}
                options={['contain', 'cover', 'fill', 'initial', 'revert', 'scale-down']}
            />
        </Grid>
        <Grid className="grid-item" item xs={4}>
            <SelectWithOptionsField
                settings={settings}
                handleChange={handleChange}
                label={'Align'}
                html_id_prefix={'cover-image-align'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.align}
                options={['center', 'left', 'right']}
            />
        </Grid>

        <Grid className="grid-heading" item xs={12}>
            <p className="with-margin">Size</p>
        </Grid>

        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Height'}
                html_id_prefix={'cover-image-height'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.height}
                with_units={true}
                units={['px', '%', 'vh', 'rem']}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Width'}
                html_id_prefix={'cover-image-width'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.width}
                with_units={true}
                units={['px', '%', 'vh', 'rem']}
            />
        </Grid>

        <Grid className="grid-heading" item xs={12}>
            <p className="with-margin">Borders</p>
        </Grid>

        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Top Left'}
                html_id_prefix={'cover-image-border-top-left'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.border_top_left}
                with_units={true}
                units={['px', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Top Right'}
                html_id_prefix={'cover-image-border-top-right'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.border_top_right}
                with_units={true}
                units={['px', '%']}
            />
        </Grid>

        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Bottom Left'}
                html_id_prefix={'cover-image-border-bottom-left'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.border_bottom_left}
                with_units={true}
                units={['px', '%']}
            />
        </Grid>
        <Grid className="grid-item" item xs={6}>
            <NumberWithUnitField
                settings={settings}
                handleChange={handleChange}
                label={'Bottom Right'}
                html_id_prefix={'cover-image-border-bottom-right'}
                key_1={UserListDesignSettings.cover_image}
                key_2={CoverImage.border_bottom_right}
                with_units={true}
                units={['px', '%']}
            />
        </Grid>
    </Grid>
}