import React, {Component} from 'react'
import PropTypes from 'prop-types'

import {getTitleCoverImage} from "../../functions";
import {connect} from "react-redux";
import TableCell from "@material-ui/core/TableCell";
import TableRow from "@material-ui/core/TableRow";
import {formatTitle, UserCardButtons} from "../cards/functions";
import {formatTitleForRows} from "./functions";
import Chip from "@material-ui/core/Chip";
import {Link} from "react-router-dom";
import {CyberLink} from "../common/common";
import CardUserEditTitleEntry from "../cards/CardUserEditTitleEntry";
import {BRANCH_EROGE} from "../../../globals";


class TitleRow extends Component {
    static propTypes = {
        /** Content Branch */
        branch: PropTypes.string.isRequired,
        /** Title Object (Anime, Manga, etc.) */
        title: PropTypes.object.isRequired,
        /** Index number for the card */
        index: PropTypes.number,
        /** Status of the user entry for this title (Watching, Reading, Completed, etc.) */
        user_status: PropTypes.string,
        /** If the card should be in a small format (No chipsets, only title) */
        mini: PropTypes.bool,
        /** If the user edit/add buttons for the title should appear */
        user_buttons: PropTypes.bool,
        /** Custom classname for the title card container */
        containerClassNames: PropTypes.string,
        /** Custom classname for the title card */
        classNames: PropTypes.string,
        /** If true it the card will not use state to check if the cover image is
         * loaded, it will directly add the img background to the card */
        skip_img_preload: PropTypes.bool,
    };

    constructor() {
        super(...arguments);
        this.state = {};
    }

    render() {
        const row_title = formatTitleForRows(this.props.branch, this.props.title, this.props.user_status);
        let img_url = getTitleCoverImage(row_title.cdn_cover_images, row_title.cover_images);

        return <TableRow>
            <TableCell component="th" scope="row" align="center">
                {this.props.index}
            </TableCell>
            <TableCell component="th" scope="row" align="center">
                <CyberLink to={row_title.link} classNames="row-image">
                    <img src={img_url}/>
                </CyberLink>
            </TableCell>
            <TableCell component="th" scope="row" className="row-title-wrapper">
                <CyberLink to={row_title.link} classNames="row-title">
                    {row_title.chipset_header ? row_title.chipset_header.map(chip => (
                        chip ? <Chip variant="default" size="small" className={`chip-${chip['color']} header-chip`}
                                     label={chip['label']}/> : null
                    )) : null}{' '}<span>{row_title.title}</span>
                </CyberLink>
            </TableCell>
            {this.props.branch !== BRANCH_EROGE ? <TableCell component="th" scope="row" align="center"
                                                             className="row-user-buttons">
                <CardUserEditTitleEntry
                    branch={this.props.branch}
                    id={row_title.id}
                    title={this.props.title}
                    user_status={this.props.user_status}
                />
            </TableCell> : null}
            {row_title.column_set.map((value, i) => (
                <TableCell component="th" scope="row" align="center" key={i}>
                    {value ? value : <span className="color-gray">---</span>}
                </TableCell>
            ))}
        </TableRow>
    }
}

const mapStateToProps = state => ({
    user_anime_display_settings: state.users.user_anime_display_settings,  // This is here to rerender the cards
    user_manga_display_settings: state.users.user_manga_display_settings,  // This is here to rerender the cards
    user_eroge_display_settings: state.users.user_eroge_display_settings,  // This is here to rerender the cards
});

export default connect(mapStateToProps, {})(TitleRow)
